import { useSelector } from "react-redux";
import { useCloseModal } from "../../../helpers/hooks";
import { calculator } from "../../../redux/calculatorSlice";
import { Text } from "../../../styledCpns/DataDisplay";
import { ModalCarpet, ModalInner } from "../../../styledCpns/Layout";
import { ExpandableList } from "../../CommonCpns";
import { calculateAndTrack } from "./helpers";
import { Attributes, Bonuses } from "./AllStats";
import Debuffs from "./Debuffs";

export default function Tracker({ index, close }) {
  const calcState = useSelector(calculator);
  const [ATTRs, bonuses, tracker] = calculateAndTrack(calcState, index);

  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <ModalInner ref={ref} className="p-4 flex-col">
        <Text
          variant="h2"
          color="orange"
          align="center"
          onClick={() => console.log(tracker)}
        >
          Tracking Results
        </Text>
        <div className="mt-3 custom-sb">
          <div>
            <ExpandableList
              headingList={["ATTRIBUTES", "BONUSES", "DEBUFFS ON TARGET"]}
              contentList={[
                <Attributes ATTRs={ATTRs} tracker={tracker} />,
                <Bonuses bonuses={bonuses} tracker={tracker} />,
                <Debuffs tracker={tracker} />
              ]}
            />
          </div>
        </div>
      </ModalInner>
    </ModalCarpet>
  );
}
