import styled from "styled-components";
import { ColoredSpan, Text } from "../../../styledCpns/DataDisplay";

export const Orange = (props) => <ColoredSpan color="orange" {...props} />;

export const Lesser = (props) => (
  <Text color="#a8a8a8" className="mt-1" {...props} />
);

export const Columns = styled.div`
  padding-left: 1rem;
  columns: 1;
  & > div {
    padding-top: 0.5rem;
    page-break-inside: avoid; /* works on Chrome */
    break-inside: avoid-column;
    display: table;
  }
  @media (min-width: 769px) {
    columns: 2;
  }
`;

export const Error = () => (
  <Text className="mt-1" color="lightRed">
    This line indicates the tracker has done something wrong!
  </Text>
);
