import getBuffedStats from "../../../calculators/buffedStats";
import getDmg from "../../../calculators/damage";
import characters from "../../../data/characters";
import { elmts, reactions } from "../../../data/elements";
import { flatSTypes, pctSTypes } from "../../../data/misc";
import { getFinalInfusion, getThisChar } from "../../../helpers/processors";

export function calculateAndTrack(
  {
    char,
    selfMCs,
    wpRack,
    subWpMCs,
    artChest,
    parties,
    elmtMCs,
    customMCs,
    target
  },
  index
) {
  const charData = characters[char.name];
  const thisChar = getThisChar(char, index);
  const infusion = getFinalInfusion(
    thisChar,
    selfMCs[index].BCs,
    charData.vision,
    parties[index]
  );
  const tracker = initTracker();
  const [ATTRs, bonuses, rxnBnes] = getBuffedStats(
    charData,
    thisChar,
    selfMCs[index].BCs,
    wpRack[index],
    subWpMCs[index].BCs,
    artChest[index],
    elmtMCs[index].resonance,
    parties[index],
    customMCs[index].BCs,
    infusion,
    tracker
  );
  const dmg = getDmg(
    thisChar,
    selfMCs[index],
    parties[index],
    artChest[index].subDCs,
    ATTRs,
    bonuses,
    rxnBnes,
    customMCs[index].DCs,
    infusion,
    elmtMCs[index],
    target,
    tracker
  );
  return [ATTRs, bonuses, tracker];
}

export const atkTypes = ["Elemental", "Physical", "NA", "CA", "PA", "ES", "EB"];
export const bnFields = ["cDmg", "cRate", "flat", "pct", "mult"];
export const reductions = ["Def", "Physical"]
  .concat(elmts)
  .map((a) => `${a}_reduct`);

function initTracker() {
  const result = {};
  for (const type of flatSTypes.concat(pctSTypes)) {
    result[type] = [];
  }
  result.AllDmg = [];
  for (const type of atkTypes) {
    result[type] = {};
    for (const field of bnFields) {
      result[type][field] = [];
    }
  }
  for (const type of reactions) {
    result[type] = [];
  }
  for (const type of reductions) {
    result[type] = [];
  }
  result.Ignore = { NA: 0, CA: 0, PA: 0, ES: 0, EB: 0 };
  result.tlBnes = { NA: {}, ES: {}, EB: {} };
  return result;
}

export const getTotal = (arr) =>
  arr.reduce((result, elmt) => result + elmt.value, 0);
