import { useState } from "react";
import { findWeapon } from "../../helpers/misc";
import { StyledRack } from "../../styledCpns/Database";
import { NoItems } from "../CommonCpns";
import Thumb from "../Readers/Thumb";

export default function LeftSection({ weapons, chosenID, setChosenID }) {
  const [clickedID, setClickedID] = useState(null);
  return (
    <StyledRack className="hide-sb">
      {weapons.length ? (
        <div className="flex-wrap">
          {weapons.map(({ type, code, ID, ...others }) => {
            const { beta, name, icon, rarity } = findWeapon({ type, code });
            return (
              <div key={ID} className="cell relative">
                <Thumb
                  item={{ beta, name, icon, rarity, ID, ...others }}
                  chosen={ID === chosenID}
                  clicked={ID === clickedID}
                  handleMouseDown={() => setClickedID(ID)}
                  choose={() => {
                    setClickedID(null);
                    setChosenID(ID);
                  }}
                />
              </div>
            );
          })}
        </div>
      ) : (
        <NoItems type="Weapons" />
      )}
    </StyledRack>
  );
}
