import { makeAdder } from "../../../../helpers/calculators";
import { countElmts } from "../../../../helpers/misc";
import { Green, LGreen } from "../../../../styledCpns/DataDisplay";
import Yelan from "./index";

export const buffs = [
  {
    index: 0,
    src: "Ascension 1 Passive Talent",
    desc: ({ char, party }) => {
      const numOfElmt = countElmts(char, party);
      const Span = ({ n, children }) => (
        <Green className={numOfElmt === n ? "" : "unavailable"}>
          {children}
        </Green>
      );
      return (
        <>
          When the party has 1/2/3/4 Elemental Types, Yelan's{" "}
          <LGreen>Max HP</LGreen> is increased by <Span n={1}>6%</Span>/
          <Span n={2}>12%</Span>/<Span n={3}>18%</Span>/<Span n={4}>30%</Span>.
        </>
      );
    },
    isGranted: (char) => char.ascension >= 1,
    affect: "self",
    addBnes: ({ ATTRs, char, party, tkDesc, tracker }) => {
      const numOfElmts = countElmts(char, party);
      let bnValue = numOfElmts * 6;
      if (numOfElmts === 4) bnValue += 6;
      makeAdder(tkDesc)(ATTRs, "HP%", bnValue, tracker);
    }
  },
  {
    index: 1,
    src: "Ascension 4 Passive Talent",
    desc: () => Yelan.pasvTalents[1].desc,
    isGranted: (char) => char.ascension >= 4,
    affect: "party",
    selfLabels: ["Stacks (max 14)"],
    labels: ["Stacks"],
    inputs: [0],
    inputTypes: ["text"],
    maxs: [14],
    addBnes: ({ bonuses, inputs, tkDesc, tracker }) => {
      makeAdder(tkDesc)(bonuses, "AllDmg", 1 + 3.5 * inputs[0], tracker);
    }
  },
  {
    index: 2,
    src: "Constellation 4",
    desc: () => Yelan.constellation[3].desc,
    isGranted: (char) => char.constellation >= 4,
    affect: "party",
    selfLabels: ["Stacks"],
    labels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [4],
    addBnes: ({ ATTRs, inputs, tkDesc, tracker }) => {
      makeAdder(tkDesc)(ATTRs, "HP%", 10 * inputs[0], tracker);
    }
  }
];
