import { useState } from "react";
import { FaExpandArrowsAlt } from "react-icons/fa";
import { TiZoom } from "react-icons/ti";
import { useSelector } from "react-redux";
import { useCloseModal } from "../../helpers/hooks";
import { calculator } from "../../redux/calculatorSlice";
import { Text } from "../../styledCpns/DataDisplay";
import { IconBtn, Select } from "../../styledCpns/Inputs";
import { AbsoluteBox, Box, ModalCarpet } from "../../styledCpns/Layout";
import { DmgTables } from "../CommonCpns/DmgTables";
import { Card } from "./styles";
import Tracker from "./Tracker";

export default function DmgResults() {
  const { name } = useSelector((state) => state.calculator.char);
  const { stdSetupI, cmpSetupIs } = useSelector((state) => state.ui);
  const [enlarged, setEnlarged] = useState(false);
  const [trackerOn, setTrackerOn] = useState(false);
  return (
    <Card className="px-3 pt-2 pb-4 flex-col relative" bgColor="cardBg3">
      {window.innerWidth >= 610 && (
        <AbsoluteBox top="0.75rem" left="0.75rem">
          <IconBtn
            className="glow-on-hover"
            variant="positive"
            size="1.75rem"
            onClick={() => setEnlarged(true)}
          >
            <FaExpandArrowsAlt />
          </IconBtn>
        </AbsoluteBox>
      )}
      {/* {cmpSetupIs.length === 1 && (
        <AbsoluteBox top="0.75rem" right="0.75rem">
          <IconBtn
            className="glow-on-hover"
            variant="neutral"
            size="1.75rem"
            onClick={() => setTrackerOn(true)}
          >
            <TiZoom size="1.25rem" />
          </IconBtn>
        </AbsoluteBox>
      )} */}
      <Inner name={name} />
      {enlarged && (
        <EnlargedInner name={name} close={() => setEnlarged(false)} />
      )}
      {trackerOn && (
        <Tracker index={stdSetupI} close={() => setTrackerOn(false)} />
      )}
    </Card>
  );
}

function EnlargedInner({ name, close }) {
  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        height="90%"
        className="px-4 pt-2 pb-3 flex-col"
        borderRadius="0.5rem"
        bgColor="cardBg3"
      >
        <Inner name={name} />
      </Box>
    </ModalCarpet>
  );
}

function Inner({ name }) {
  const { setups, allDmg } = useSelector(calculator);
  const { stdSetupI, cmpSetupIs } = useSelector((state) => state.ui);
  const [focus, setFocus] = useState("Average");
  return (
    <>
      {cmpSetupIs.length > 1 ? (
        <div className="mb-3 flex justify-center">
          <Text className="mr-2">Choose a focus</Text>
          <Select
            fontSize="1rem"
            align="center"
            color="lightGold"
            value={focus}
            onChange={(e) => setFocus(e.target.value)}
          >
            {["Non-crit", "Crit", "Average"].map((opt) => (
              <option key={opt}>{opt}</option>
            ))}
          </Select>
        </div>
      ) : (
        <Text className="mx-3 my-2" fontWeight={700} align="center">
          {setups[stdSetupI].name.toUpperCase()}
        </Text>
      )}
      <div className="grow-1 hide-sb">
        <DmgTables
          resetSignal={name}
          charName={name}
          dmg={allDmg[stdSetupI]}
          focus={cmpSetupIs.length > 1 ? focus : null}
        />
      </div>
    </>
  );
}
