import { artMS } from "../data/artifacts/config";
import { flatSTypes, pctSTypes } from "../data/misc";
import { findArtifact, findWeapon } from "../helpers/misc";

export function initiateATTRs(charData, char, wpData, weapon, tracker) {
  const ATTRs = {};
  for (const type of flatSTypes.concat(pctSTypes)) {
    ATTRs[type] = 0;
  }
  const stats = { ...charData.stats[char.level] };
  stats["CRIT Rate"] = (stats["CRIT Rate"] || 0) + 5;
  stats["CRIT DMG"] = (stats["CRIT DMG"] || 0) + 50;
  stats["Energy Recharge"] = (stats["Energy Recharge"] || 0) + 100;
  stats["Normal ATK SPD"] = stats["Charged ATK SPD"] = 100;
  for (const key in stats) {
    ATTRs[key] = (ATTRs[key] || 0) + stats[key];
    if (tracker) {
      const field = key.slice(0, 4) === "Base" ? key.slice(5) : key;
      tracker[field].push({ desc: "Character Base Stat", value: stats[key] });
    }
  }
  const wpAtk = wpData.mainStat.value[weapon.level];
  ATTRs["Base ATK"] += wpAtk;
  if (tracker) {
    tracker.ATK.push({ desc: "Weapon Main Stat", value: wpAtk });
  }
  return ATTRs;
}

export function addArtStats(pieces, ATTRs, tracker) {
  const artSBnes = { HP: 0, ATK: 0, DEF: 0 };
  for (const artP of pieces) {
    if (artP) {
      const { type, level, rarity = 5, mainSType, subS } = artP;
      const mainStat = artMS[type][mainSType][rarity][level];
      artSBnes[mainSType] = (artSBnes[mainSType] || 0) + mainStat;
      if (tracker) {
        tracker[mainSType].push({
          desc: type[0].toUpperCase() + type.slice(1),
          value: mainStat
        });
      }
      for (const ss of subS) {
        const subStat = Number(ss.val);
        artSBnes[ss.type] = (artSBnes[ss.type] || 0) + subStat;
        if (tracker) {
          const desc = "Art Sub Stats";
          const existed = tracker[ss.type].find((tk) => tk.desc === desc);
          if (existed) {
            existed.value += subStat;
          } else {
            tracker[ss.type].push({ desc, value: subStat });
          }
        }
      }
    }
  }
  for (const type of ["HP", "ATK", "DEF"]) {
    const pctStats = artSBnes[type + "%"];
    if (pctStats) {
      artSBnes[type] += Math.round((ATTRs["Base " + type] * pctStats) / 100);
    }
    delete artSBnes[type + "%"];
  }
  for (const type in artSBnes) {
    ATTRs[type] += artSBnes[type];
  }
  return artSBnes;
}

export function addWpSubStats(wpData, ATTRs, wpLevel, tracker) {
  if (wpData.subStats) {
    for (const stat of wpData.subStats) {
      const subStat = stat.value[wpLevel];
      ATTRs[stat.type] += subStat;
      if (tracker) {
        tracker[stat.type].push({ desc: "Weapon Sub Stat", value: subStat });
      }
    }
  }
}

export function addArtBonuses(isFinal, sets, wrapper) {
  for (const { code, bonusLv } of sets) {
    for (let i = 0; i <= bonusLv; i++) {
      const { addBnes, addFinalBnes } = findArtifact({ code }).setBnes[i];
      if (!isFinal && addBnes) {
        addBnes(wrapper);
      } else if (isFinal && addFinalBnes) {
        addFinalBnes(wrapper);
      }
    }
  }
}

export function addWpBonuses(isFinal, wpData, wrapper, wpRfm, party) {
  const { addBnes, addFinalBnes } = wpData;
  if (!isFinal && addBnes) {
    addBnes({ ...wrapper, wpRfm, party });
  } else if (isFinal && addFinalBnes) {
    // v2.3 - Homa, Jade Cutter, EL, Moonglow
    addFinalBnes({ ...wrapper, wpRfm });
  }
}

export function calcFinalATTRs(ATTRs) {
  ["HP", "ATK", "DEF"].forEach((type) => {
    ATTRs[type] += Math.round(
      ATTRs["Base " + type] * (1 + ATTRs[type + "%"] / 100)
    );
    delete ATTRs[type + "%"];
  });
}

export default function getBaseStats(charData, char, weapon, art) {
  if (!weapon) return [{}, {}];
  const wpData = findWeapon(weapon);
  const ATTRs = initiateATTRs(charData, char, wpData, weapon);
  const artSBnes = addArtStats(art.pieces, ATTRs);
  addWpSubStats(wpData, ATTRs, weapon.level);

  const wrapper = { ATTRs, charData };
  addArtBonuses(false, art.sets, wrapper);
  addWpBonuses(false, wpData, wrapper, weapon.refinement);
  calcFinalATTRs(ATTRs);
  return [ATTRs, artSBnes];
}
