import { useEffect, useState } from "react";
import { FaSort, FaTh } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import characters from "../../data/characters";
import { wikia } from "../../data/misc";
import { getIndexByName } from "../../helpers/misc";
import { CHOOSE_MY_CHAR } from "../../redux/databaseSlice";
import { Text } from "../../styledCpns/DataDisplay";
import { IconBtn } from "../../styledCpns/Inputs";
import { AbsoluteBox, Box } from "../../styledCpns/Layout";
import Picker from "../Picker";
import CharSorting from "./CharSorting";
import { StyledTopBar } from "./styles";

export default function TopBar({ setPickerOn, charListRef }) {
  const [sorting, setSorting] = useState(false);
  const [gridview, setGridview] = useState(false);
  const { myChars, chosenChar } = useSelector((state) => state.database);

  useEffect(() => {
    scrolLeft(charListRef, myChars, chosenChar);
    const thisCpn = charListRef.current;
    const scrollHorizontally = (e) => {
      const delta = Math.max(-1, Math.min(1, e.wheelDelta || -e.detail));
      thisCpn.scrollLeft -= delta * 50;
      e.preventDefault();
    };
    thisCpn.addEventListener("wheel", scrollHorizontally);
    return () => thisCpn.removeEventListener("wheel", scrollHorizontally);
  }, [charListRef]);

  return (
    <Box className="full-w flex justify-center" bgColor="cardBg2">
      <StyledTopBar>
        {!!myChars.length && (
          <AbsoluteBox top="2rem" right="100%" className="flex">
            <IconBtn
              className="mr-3 glow-on-hover"
              variant="positive"
              fontSize="1.25rem"
              onClick={() => setSorting(true)}
            >
              <FaSort />
            </IconBtn>
            <IconBtn
              className="mr-3 glow-on-hover"
              variant="positive"
              onClick={() => setGridview(true)}
            >
              <FaTh />
            </IconBtn>
          </AbsoluteBox>
        )}
        <div ref={charListRef} className="mt-2 flex full-w hide-sb">
          {myChars.length ? (
            <CharList myChars={myChars} chosenChar={chosenChar} />
          ) : (
            <Box
              className="full-w flex justify-end align-center"
              height="4.875rem"
            >
              <Text variant="h2" color="lightGold">
                Add New Characters {">>"}
              </Text>
            </Box>
          )}
        </div>
        <img
          src={wikia + "4/48/System_Wish.png"}
          alt="wish"
          className="wish_btn pointer"
          draggable={false}
          onClick={() => setPickerOn(true)}
        />
      </StyledTopBar>
      {sorting && <CharSorting close={() => setSorting(false)} />}
      {gridview && (
        <CharPicker
          charListRef={charListRef}
          close={() => setGridview(false)}
        />
      )}
    </Box>
  );
}

function CharList({ myChars, chosenChar }) {
  const dispatch = useDispatch();

  return myChars.map(({ name }) => {
    const { beta, icon, sideIcon } = characters[name];
    const xtraClasses = beta
      ? "beta_wrapper m-1 overflow-hidden"
      : "side_wrapper m-2";
    return (
      <div
        key={name}
        className={"mx-1 cell pointer" + (name === chosenChar ? " chosen" : "")}
        onClick={() => dispatch(CHOOSE_MY_CHAR(name))}
      >
        <div className={"icon_wrapper " + xtraClasses}>
          <img
            src={wikia + (beta ? icon : sideIcon) + ".png"}
            alt="icon"
            draggable={false}
          />
        </div>
      </div>
    );
  });
}

function CharPicker({ charListRef, close }) {
  const { myChars } = useSelector((state) => state.database);
  const dispatch = useDispatch();

  const data = myChars.map(({ name, constellation }) => {
    const { code, icon, rarity, vision, weapon } = characters[name];
    return { code, name, constellation, icon, rarity, vision, weapon };
  });
  return (
    <Picker
      data={data}
      dataType="Character"
      close={close}
      pick={({ name }) => {
        dispatch(CHOOSE_MY_CHAR(name));
        scrolLeft(charListRef, myChars, name);
      }}
    />
  );
}

function scrolLeft(charListRef, myChars, name) {
  if (charListRef.current) {
    charListRef.current.scrollLeft = getIndexByName(myChars, name) * 84;
  }
}
