import { useState } from "react";
import { FaInfoCircle, FaBars } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import { useCloseModal } from "../../helpers/hooks";
import { CHANGE_TAB, TOGGLE_INTRO } from "../../redux/uiSlice";
import { Text } from "../../styledCpns/DataDisplay";
import { IconBtn } from "../../styledCpns/Inputs";
import { Box, ModalCarpet } from "../../styledCpns/Layout";
import Download from "./Download";
import { BarsBtn, NavBtn, NavRight, StyledMenu } from "./styles";
import Upload from "./Upload";

export default function NavBar(props) {
  return (
    <Box bgColor="rgba(0, 0, 0, 0.6)">
      {window.innerWidth >= 1050 ? (
        <PcInner {...props} />
      ) : (
        <MobileInner {...props} />
      )}
    </Box>
  );
}

function IntroBtn() {
  const dispatch = useDispatch();
  return (
    <NavBtn
      className="last-btn border pointer contain-info_sign"
      onClick={() => dispatch(TOGGLE_INTRO(true))}
    >
      <IconBtn className="mr-2 info_sign" size="1.125rem" fontSize="1.125rem">
        <FaInfoCircle />
      </IconBtn>
      <Text fontWeight={700}>Introduction</Text>
    </NavBtn>
  );
}

function PcInner({ loadData, outdates }) {
  const { tab } = useSelector((state) => state.ui);
  const dispatch = useDispatch();
  return (
    <div className="flex justify-between">
      <div className="flex">
        {["Characters", "Weapons", "Artifacts", "Setups", "Calculator"].map(
          (item, i) => (
            <NavBtn
              key={i}
              className={"px-2 py-1" + (item === tab ? " current" : "")}
              onClick={() => dispatch(CHANGE_TAB(item))}
            >
              <Text className="pointer" fontWeight={700}>
                {i < 4 && "My"} {item}
              </Text>
            </NavBtn>
          )
        )}
      </div>
      <NavRight className="px-1 flex">
        <IntroBtn />
        <Download />
        <Upload loadData={loadData} outdates={outdates} />
      </NavRight>
    </div>
  );
}

function MobileInner(props) {
  const [showing, setShowing] = useState(false);
  return (
    <div className="flex">
      <div className="mr-auto relative">
        <BarsBtn
          className={showing ? "showing" : ""}
          onClick={() => setShowing(!showing)}
        >
          <FaBars />
        </BarsBtn>
        {showing && <Menu {...props} close={() => setShowing(false)} />}
      </div>
    </div>
  );
}

function Menu({ loadData, close }) {
  const { tab } = useSelector((state) => state.ui);
  const dispatch = useDispatch();
  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <StyledMenu
        ref={ref}
        className="white-glow-shadow overflow-hidden"
        borderRadius="0.25rem"
      >
        <IntroBtn />
        {["Characters", "Weapons", "Artifacts", "Setups", "Calculator"].map(
          (item, i) => (
            <NavBtn
              key={i}
              className={"px-4 py-2 border" + (item === tab ? " current" : "")}
              onClick={() => {
                dispatch(CHANGE_TAB(item));
                close();
              }}
            >
              <Text className="pointer" fontWeight={700}>
                {i < 4 && "My "}
                {item}
              </Text>
            </NavBtn>
          )
        )}
        <Download />
        <Upload loadData={loadData} closeMenu={close} />
      </StyledMenu>
    </ModalCarpet>
  );
}
