import { makeAdder } from "../../../../helpers/calculators";
import { getElmtByIndex } from "../../../../helpers/misc";
import { Red } from "../../../../styledCpns/DataDisplay";
import Shinobu from "./index";

export const buffs = [
  {
    index: 0,
    src: "Ascension 1 Passive Talent",
    desc: () => Shinobu.pasvTalents[0].desc,
    isGranted: (char) => char.ascension >= 1,
    affect: "self",
    addBnes: ({ ATTRs, tkDesc, tracker }) => {
      makeAdder(tkDesc)(ATTRs, "Healing Bonus", 15, tracker);
    }
  },
  {
    index: 1,
    src: "Ascension 4 Passive Talent",
    desc: ({ ATTRs }) => {
      const [heal, dmg] = getElmtByIndex(Shinobu.buffs, 1).bnValue(ATTRs);
      return (
        <>
          {Shinobu.pasvTalents[1].lines[0]}{" "}
          <Red>Healing amount bonus: {heal}.</Red>
          {Shinobu.pasvTalents[1].lines[1]} <Red>DMG bonus: {dmg}.</Red>
        </>
      );
    },
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    bnValue: ({ "Elemental Mastery": EM }) => [
      Math.round(EM * 0.75),
      Math.round(EM * 0.25)
    ]
  },
  {
    index: 2,
    src: "Constellation 6",
    desc: () => Shinobu.constellation[5].extra,
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    addBnes: ({ ATTRs, tkDesc, tracker }) => {
      makeAdder(tkDesc)(ATTRs, "Elemental Mastery", 150, tracker);
    }
  }
];
