import { Columns, Lesser, Orange } from "./styles";
import { reductions, getTotal } from "./helpers";
import { LGreen, Text } from "../../../styledCpns/DataDisplay";

export default function Debuffs({ tracker }) {
  return (
    <Columns>
      {reductions.map((reduct) => {
        const total = getTotal(tracker[reduct]);
        if (!total) return null;
        let heading = reduct.slice(0, -7);
        if (heading === "Def") {
          heading = "Defense";
        } else {
          heading += " RES";
        }
        return (
          <div key={reduct}>
            <Text fontWeight={700}>
              {heading} Reduction <Orange>{total}%</Orange>
            </Text>
            <div className="px-2">
              {tracker[reduct].map(({ desc, value }, i) => (
                <Lesser key={i}>
                   {desc}: <LGreen>{value}%</LGreen>
                </Lesser>
              ))}
            </div>
          </div>
        );
      })}
    </Columns>
  );
}
