import { getEBcost, makeAdder } from "../../helpers/calculators";
import { Green, LGreen, Red } from "../../styledCpns/DataDisplay";
import characters from "../characters";

const addBoB = (name, wpRfm, isBonus) =>
  makeAdder(`${name} R${wpRfm || "?"} ${isBonus ? `bonuse` : `activated`}`);

export const RoyalSeries = (name) => ({
  buffs: [
    {
      index: 0,
      affect: "self",
      labels: ["Stacks"],
      inputTypes: ["stacks"],
      values: [5],
      addBnes: ({ ATTRs, wpRfm, inputs, tracker }) => {
        const bnValue = (6 + wpRfm * 2) * inputs[0];
        addBoB(name, wpRfm)(ATTRs, "CRIT Rate", bnValue, tracker);
      },
      desc: ({ wpRfm }) => RoyalSeries.pasvDescs({ wpRfm })[0].buff
    }
  ],
  pasvName: "Focus",
  pasvDescs: ({ wpRfm }) => [
    {
      buff: (
        <>
          Upon dealing damage to an opponent, increases{" "}
          <LGreen>CRIT Rate</LGreen> by <Green>{6 + wpRfm * 2}%</Green>. Max{" "}
          <Green>5</Green> stacks. A CRIT hit removes all existing stacks.
        </>
      )
    }
  ]
});

