import { Text } from "../../../styledCpns/DataDisplay";
import { NoContent } from "../../Calculator/Modifiers/CmnCpns";
import { ExpandableList, AmpRxnDesc } from "../../CommonCpns";
import { ModReader } from "./styles";
import characters from "../../../data/characters";
import artifactBuffs from "../../../data/artifacts/artifactBuffs";
import { rsnBuffDesc } from "../../../data/elements";
import {
  findArtifact,
  findWeapon,
  getElmtByIndex,
  pctOf
} from "../../../helpers/misc";

export default function Buffs(props) {
  const headingList = [
    "Resonance & Reactions",
    "Self",
    "Party",
    "Weapons",
    "Artifacts",
    "Custom"
  ];
  return (
    <ExpandableList
      headingList={headingList}
      contentList={headingList.map((cate, i) => (
        <Inner key={i} cate={cate} {...props} />
      ))}
    />
  );
}

function Inner(props) {
  const { cate, char, party, ATTRs } = props;
  let content = [];
  if (cate === "Resonance & Reactions") {
    const { resonance, ampRxn, naAmpRxn } = props.elmtMCs;
    for (const { name } of resonance) {
      content.push(
        <ModReader key={name} heading={name} desc={rsnBuffDesc[name]} />
      );
    }
    if (ampRxn) {
      content.push(
        <ModReader
          key="ampRxn"
          heading={ampRxn}
          desc={
            <AmpRxnDesc
              elmt={characters[char.name].vision}
              mult={props.rxnBnes[ampRxn]}
            />
          }
        />
      );
    }
    if (naAmpRxn) {
      content.push(
        <ModReader
          key="naAmpRxn"
          heading={naAmpRxn + " (external infusion)"}
          desc={
            <AmpRxnDesc
              elmt={props.infusion.NA}
              mult={props.rxnBnes["na" + naAmpRxn]}
            />
          }
        />
      );
    }
  } else if (cate === "Self") {
    const { selfBCs } = props;
    content = selfBCs.map(({ index, inputs }) => {
      const buff = getElmtByIndex(characters[char.name].buffs, index);
      return (
        <ModReader
          key={index}
          heading={buff.src}
          desc={buff.desc({
            toSelf: true,
            ATTRs,
            char,
            charBCs: selfBCs,
            inputs,
            party
          })}
          labels={buff.selfLabels}
          inputTypes={buff.inputTypes}
          inputs={inputs}
        />
      );
    });
  } else if (cate === "Party") {
    for (const { name, BCs } of party.filter((tm) => tm && tm.BCs.length)) {
      const { vision, buffs } = characters[name];
      content.push(
        <Text
          key={name}
          className="pt-2 mb-m1"
          variant="h6"
          fontWeight={700}
          color={vision}
          align="center"
        >
          {name.toUpperCase()}
        </Text>
      );
      for (const { index, inputs } of BCs) {
        const buff = getElmtByIndex(buffs, index);
        content.push(
          <ModReader
            key={name + index}
            heading={buff.src}
            desc={buff.desc({
              toSelf: false,
              char,
              charBCs: BCs,
              inputs,
              party
            })}
            labels={buff.labels}
            inputTypes={buff.inputTypes}
            inputs={inputs}
          />
        );
      }
    }
  } else if (cate === "Weapons") {
    const { weapon, subWpBCs } = props;
    for (const { index, inputs } of weapon.BCs) {
      const { name, buffs } = findWeapon(weapon);
      const buff = getElmtByIndex(buffs, index);
      content.push(
        <ModReader
          key={weapon.code.toString() + index}
          heading={name}
          desc={buff.desc({ wpRfm: weapon.refinement, ATTRs })}
          labels={buff.labels}
          inputTypes={buff.inputTypes}
          inputs={inputs}
        />
      );
    }
    for (const type in subWpBCs) {
      for (const ctrl of subWpBCs[type]) {
        const { code, refinement: wpRfm, index } = ctrl;
        const { name, buffs, pasvDescs } = findWeapon({ type, code });
        const buff = getElmtByIndex(buffs, index);
        content.push(
          <ModReader
            key={"sub" + code + index}
            heading={name}
            desc={pasvDescs({ wpRfm })[0].buff}
            labels={["Refinement", ...(buff.labels || [])]}
            inputTypes={["text", ...(buff.inputTypes || [])]}
            inputs={[wpRfm, ...(ctrl.inputs || [])]}
          />
        );
      }
    }
  } else if (cate === "Artifacts") {
    const { sets, BCs, subBCs } = props.arts;
    const mainCode = sets[0]?.code;
    for (const { index, inputs } of BCs) {
      const buff = artifactBuffs[mainCode][index];
      content.push(
        <ModReader
          key={mainCode.toString() + index}
          heading={findArtifact({ code: mainCode }).name + " (self)"}
          desc={buff.desc()}
          labels={buff.labels}
          inputTypes={buff.inputTypes}
          inputs={inputs}
        />
      );
    }
    for (const { code, index, inputs } of subBCs) {
      const buff = artifactBuffs[code][index];
      content.push(
        <ModReader
          key={code.toString() + index}
          heading={findArtifact({ code }).name}
          desc={buff.desc()}
          labels={buff.labels}
          inputTypes={buff.inputTypes}
          inputs={inputs}
        />
      );
    }
  } else if (cate === "Custom") {
    content = props.customBCs.map(({ catKey, type, val }, i) => (
      <div key={i} className="pt-2 flex justify-end">
        <Text className="mr-3">{type}</Text>
        <Text color="orange" fontWeight={700}>
          {val}
          {catKey > 1 ? "%" : pctOf(type)}
        </Text>
      </div>
    ));
  }
  return content.length ? content : <NoContent type="buffs" />;
}
