import characters from "../data/characters";
import { getElmtByIndex } from "./misc";

export function getEBcost(name) {
  if (name) {
    for (let tl of characters[name].actvTalents) {
      if (tl.type === "Elemental Burst") return tl.energyCost;
    }
  }
  return 0;
}

export const allNAs = ["NA.pct", "CA.pct", "PA.pct"];

export const convert = {
  pct: "DMG",
  flat: "Flat",
  cRate: "CR",
  cDmg: "CD",
  mult: "mult"
};

export function makeAdder(desc) {
  return (target, paths, value, tracker) => {
    (Array.isArray(paths) ? paths : [paths]).forEach((path, i) => {
      const routes = path.split(".");
      const thisValue = Array.isArray(value) ? value[i] : value;
      const node = { desc, value: thisValue };
      if (routes.length === 1) {
        target[routes[0]] += thisValue;
        if (tracker) {
          tracker[routes[0]].push(node);
        }
      } else {
        target[routes[0]][convert[routes[1]]] += thisValue;
        if (tracker) {
          tracker[routes[0]][routes[1]].push(node);
        }
      }
    });
  };
}

export function incElementalBnes(settings, bonuses, desc, tracker) {
  const { elmt, bnType, bnValue: value } = settings;
  if (settings.vision === elmt) {
    bonuses.Elemental[convert[bnType]] += value;
    if (tracker) {
      tracker.Elemental[bnType].push({ desc, value });
    }
  }
  for (const type of ["NA", "CA", "PA"]) {
    if (settings.infusion[type] === elmt) {
      bonuses[type][convert[bnType]] += value;
      if (tracker) {
        tracker[type][bnType].push({ desc, value });
      }
    }
  }
}

export function makeSubtractor(desc) {
  return (rdMult, paths, value, tracker) => {
    (Array.isArray(paths) ? paths : [paths]).forEach((path, i) => {
      const routes = path.split(".");
      const thisValue = Array.isArray(value) ? value[i] : value;
      const node = { desc, value: thisValue };
      if (routes.length === 1) {
        rdMult[routes[0]] -= thisValue;
        if (tracker) {
          let trackerPath = routes[0];
          if (path.slice(-3) === "RES") {
            // path = ${elmt} RES
            trackerPath = path.slice(0, -4);
          }
          tracker[`${trackerPath}_reduct`].push(node);
        }
      } else {
        rdMult.Ignore[routes[1]] -= thisValue;
        if (tracker) {
          tracker.Ignore[routes[1]].push(node);
        }
      }
    });
  };
}

export function checkCharMC(mods, char, charBCs, index) {
  const ctrl = getElmtByIndex(charBCs, index);
  return getElmtByIndex(mods, index)?.isGranted(char) && ctrl && ctrl.activated;
}

export function findInput(MCs, ctrlI, inpI, defaultVal = 0) {
  const ctrl = getElmtByIndex(MCs, ctrlI);
  return ctrl && ctrl.inputs ? ctrl.inputs[inpI] : defaultVal;
}
