import { getAcro } from "../data/acro";
import artifactBuffs from "../data/artifacts/artifactBuffs";
import characters from "../data/characters";
import { ampRxnTrigElmts, reactions, tfmRxns } from "../data/elements";
import { findWeapon, getElmtByIndex } from "../helpers/misc";
import {
  addArtBonuses,
  addArtStats,
  addWpBonuses,
  addWpSubStats,
  calcFinalATTRs,
  initiateATTRs
} from "./baseStats";
import { getRxnBnesFromEM } from "./helpers";

function addCustomBuffs({ ATTRs, bonuses, rxnBnes, tracker }, BCs) {
  for (const { catKey, type, val } of BCs) {
    const node = { desc: "Custom Buff", value: val };
    if (catKey < 2) {
      ATTRs[type] += val;
      if (tracker) tracker[type].push(node);
    } else if (catKey === 2) {
      const thisType = getAcro(type);
      bonuses[thisType].DMG += val;
      if (tracker) tracker[thisType].pct.push(node);
    } else {
      rxnBnes[type] += val;
      if (tracker) tracker[type].push(node);
    }
  }
}

function addResonanceBuffs(ATTRs, bonuses, resonance, tracker) {
  for (let rsn of resonance) {
    if (rsn.activated) {
      const { field, value, elmt } = {
        "Fervent Flames": { field: "ATK%", value: 25, elmt: "Pyro" },
        "Shattering Ice": { field: "CRIT Rate", value: 15, elmt: "Cryo" },
        "Enduring Rock": { field: "Shield Strength", value: 15, elmt: "Geo" }
      }[rsn.name];
      ATTRs[field] += value;
      const desc = `${elmt} Resonance`;
      if (tracker) tracker[field].push({ desc, value });
      if (rsn.name === "Enduring Rock") {
        bonuses.AllDmg += 15;
        if (tracker) tracker.AllDmg.push({ desc, value: 15 });
      }
    }
  }
}

function addWpBuffs(wrapper, weapon, subWpBCs, wpBuffs) {
  for (const type in subWpBCs) {
    for (const ctrl of subWpBCs[type]) {
      if (ctrl.activated) {
        const { code, refinement, inputs } = ctrl;
        const { buffs } = findWeapon({ type, code });
        const { addBnes } = getElmtByIndex(buffs, ctrl.index);
        addBnes({ ...wrapper, wpRfm: refinement, inputs });
      }
    }
  }
  for (const ctrl of weapon.BCs) {
    const { addBnes } = getElmtByIndex(wpBuffs, ctrl.index);
    if (ctrl.activated && addBnes) {
      const { refinement: wpRfm } = weapon;
      addBnes({ ...wrapper, wpRfm, inputs: ctrl.inputs });
    }
  }
}

function addArtBuffs(wrapper, art) {
  for (const ctrl of art.subBCs) {
    if (ctrl.activated) {
      const { addBnes } = artifactBuffs[ctrl.code][ctrl.index];
      addBnes({ ...wrapper, inputs: ctrl.inputs });
    }
  }
  for (const ctrl of art.BCs) {
    const { addBnes } = artifactBuffs[art.sets[0].code][ctrl.index];
    if (ctrl.activated && addBnes) {
      addBnes({ ...wrapper, inputs: ctrl.inputs });
    }
  }
}

function addTeammateBuffs(party, wrapper, char, infusion) {
  for (const tm of party) {
    if (!tm) continue;
    const tmData = characters[tm.name];
    for (const ctrl of tm.BCs) {
      const { index, activated, inputs } = ctrl;
      const buff = getElmtByIndex(tmData.buffs, index);
      if (buff) {
        const fn = buff.addBnes || buff.addFinalBnes;
        if (activated && fn) {
          fn({
            ...wrapper,
            char,
            charBCs: tm.BCs,
            toSelf: false,
            inputs,
            infusion,
            party,
            tkDesc: `${tm.name} / ${buff.src}`
          });
        }
      }
    }
  }
}

function addWpFinalBuffs(ATTRs, weapon, buffs, tracker) {
  for (let ctrl of weapon.BCs) {
    if (ctrl.activated) {
      const { addFinalBnes } = getElmtByIndex(buffs, ctrl.index);
      if (addFinalBnes) {
        addFinalBnes({ ATTRs, wpRfm: weapon.refinement, tracker });
      }
    }
  }
}

function addArtFinalBuffs(ATTRs, bonuses, art, tracker) {
  for (let ctrl of art.BCs) {
    const { addFinalBnes } = artifactBuffs[art.sets[0].code][ctrl.index];
    if (ctrl.activated && addFinalBnes) {
      addFinalBnes({ ATTRs, bonuses, tracker });
    }
  }
}

function addSelfBuffs(isFinal, charBCs, wrapper, char, infusion, party) {
  for (let ctrl of charBCs) {
    const buff = getElmtByIndex(characters[char.name].buffs, ctrl.index);
    if (buff && buff.isGranted(char) && ctrl.activated) {
      let fn = null;
      if (!isFinal && buff.addBnes) fn = buff.addBnes;
      else if (isFinal && buff.addFinalBnes) fn = buff.addFinalBnes;
      if (fn) {
        fn({
          ...wrapper,
          char,
          charBCs,
          inputs: ctrl.inputs,
          toSelf: true,
          infusion,
          party,
          tkDesc: `Self / ${buff.src}`
        });
      }
    }
  }
}

function calcFinalRxnBnes(ATTRs, rxnBnes, vision, infusion) {
  const { transformative, amplifying } = getRxnBnesFromEM(
    ATTRs["Elemental Mastery"]
  );
  Object.keys(tfmRxns).forEach((rxn) => {
    rxnBnes[rxn] += transformative;
  });
  Object.keys(ampRxnTrigElmts).forEach((rxn) => {
    rxnBnes[rxn] += amplifying;
  });
  const meltBn = rxnBnes.Melt;
  const vapBn = rxnBnes.Vaporize;
  rxnBnes.Melt = (vision === "Pyro" ? 2 : 1.5) * (1 + meltBn / 100);
  rxnBnes.Vaporize = (vision === "Pyro" ? 1.5 : 2) * (1 + vapBn / 100);
  if (infusion.NA !== vision) {
    rxnBnes.naMelt = (infusion.NA === "Pyro" ? 2 : 1.5) * (1 + meltBn / 100);
    rxnBnes.naVaporize = (infusion.NA === "Pyro" ? 1.5 : 2) * (1 + vapBn / 100);
  }
}

export default function getBuffedStats(
  charData,
  char,
  charBCs,
  weapon,
  subWpBCs,
  art,
  resonance,
  party,
  customBCs,
  infusion,
  tracker
) {
  const wpData = findWeapon(weapon);
  const ATTRs = initiateATTRs(charData, char, wpData, weapon, tracker);
  const artSBnes = addArtStats(art.pieces, ATTRs, tracker);
  addWpSubStats(wpData, ATTRs, weapon.level, tracker);

  const bonuses = {
    AllDmg: 0,
    Elemental: { DMG: 0, Flat: 0, CR: 0, CD: 0 },
    Physical: { DMG: 0, Flat: 0, CR: 0, CD: 0 },
    NA: { DMG: 0, Flat: 0, CR: 0, CD: 0, mult: 0 },
    CA: { DMG: 0, Flat: 0, CR: 0, CD: 0 },
    PA: { DMG: 0, Flat: 0, CR: 0, CD: 0 },
    ES: { DMG: 0, Flat: 0, CR: 0, CD: 0, mult: 0 },
    EB: { DMG: 0, Flat: 0, CR: 0, CD: 0 }
  };
  const rxnBnes = {};
  for (const rxn of reactions) {
    rxnBnes[rxn] = 0;
  }
  const wrapper = { ATTRs, bonuses, rxnBnes, charData, tracker };

  addWpBonuses(false, wpData, wrapper, weapon.refinement, party);
  addArtBonuses(false, art.sets, wrapper);
  // charData is not needed
  addCustomBuffs(wrapper, customBCs);
  addResonanceBuffs(ATTRs, bonuses, resonance, tracker);
  // rxnBnes is not needed yet
  addWpBuffs(wrapper, weapon, subWpBCs, wpData.buffs);
  addArtBuffs({ ATTRs, bonuses, tracker }, art);
  addTeammateBuffs(party, wrapper, char, infusion);
  // charData is not needed yet
  addSelfBuffs(false, charBCs, wrapper, char, infusion, party);

  calcFinalATTRs(ATTRs);
  addArtBonuses(true, art.sets, wrapper);
  addWpBonuses(true, wpData, wrapper, weapon.refinement, party);
  addWpFinalBuffs(ATTRs, weapon, wpData.buffs, tracker);
  addArtFinalBuffs(ATTRs, bonuses, art, tracker);
  addSelfBuffs(true, charBCs, wrapper, char, infusion, party);

  bonuses.Elemental.DMG += ATTRs[charData.vision + " DMG Bonus"];
  bonuses.Physical.DMG += ATTRs["Physical DMG Bonus"];
  calcFinalRxnBnes(ATTRs, rxnBnes, charData.vision, infusion);
  return [ATTRs, bonuses, rxnBnes, artSBnes];
}

