import { useState } from "react";
import { useCloseModal } from "../../helpers/hooks";
import { Box, ModalCarpet, ModalInner } from "../../styledCpns/Layout";
import Body from "./Body";
import Heading from "./Heading";

const initFilter = { type: "", value: "" };

export default function Picker({ data, dataType, needMassAdd, pick, close }) {
  const [filter, setFilter] = useState(initFilter);

  const InnerPicker = needMassAdd ? MassPicker : SinglePicker;
  const filteredData = filter.type
    ? data.filter((unit) => unit[filter.type] === filter.value)
    : [...data];

  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <ModalInner ref={ref}>
        <InnerPicker
          dataType={dataType}
          data={filteredData}
          filter={filter}
          setFilter={(chosen, newFilter) =>
            setFilter(chosen ? initFilter : newFilter)
          }
          pick={pick}
          close={close}
        />
      </ModalInner>
    </ModalCarpet>
  );
}

function SinglePicker({ data, dataType, filter, setFilter, pick, close }) {
  return (
    <>
      <Box height="10%" className="p-2">
        <Heading
          dataType={dataType}
          close={close}
          needMassAdd={false}
          filter={filter}
          setFilter={setFilter}
        />
      </Box>
      <Box height="90%" className="px-3 pt-2 pb-3">
        <Body
          forChar={dataType === "Character"}
          data={data}
          pick={(item) => {
            pick(item);
            close();
          }}
        />
      </Box>
    </>
  );
}

function MassPicker({ data, dataType, filter, setFilter, pick, close }) {
  const [massAdd, setMassAdd] = useState(false);
  const [total, setTotal] = useState(0);
  const [each, setEach] = useState(data.map(() => 0));
  return (
    <>
      <Box height="10%" className="p-2">
        <Heading
          dataType={dataType}
          needMassAdd
          massAdd={massAdd}
          setMassAdd={setMassAdd}
          count={total}
          filter={filter}
          setFilter={setFilter}
          close={close}
        />
      </Box>
      <Box height="90%" className="px-3 pt-2 pb-3">
        <Body
          forChar={dataType === "Character"}
          data={data}
          massAdd={massAdd}
          each={each}
          pick={(item, i) => {
            pick(item);
            if (!massAdd) {
              close();
            } else {
              setTotal((prev) => prev + 1);
              if (dataType !== "Character")
                setEach((prev) => {
                  const arr = [...prev];
                  arr[i]++;
                  return arr;
                });
            }
          }}
        />
      </Box>
    </>
  );
}
