import { FaUpload } from "react-icons/fa";
import { useEffect, useRef, useState } from "react";
import convertToGOOD from "../../helpers/GOODconverter";
import { useCloseModal } from "../../helpers/hooks";
import { Text } from "../../styledCpns/DataDisplay";
import { Button } from "../../styledCpns/Inputs";
import { ModalCarpet } from "../../styledCpns/Layout";
import { CloseBtn } from "../CommonCpns";
import { Divider, NavBtn, LoadOptions } from "./styles";

const uploadErrMs =
  "An Error has occured while loading your data. Please contact me for this matter.";

export default function Upload({ outdates, loadData, closeMenu, uploadErr }) {
  const [optionsOn, setOptionsOn] = useState(false);
  return (
    <div>
      <NavBtn className="last-btn pointer" onClick={() => setOptionsOn(true)}>
        <FaUpload className="mr-2" />
        <Text fontWeight={700}>Upload</Text>
      </NavBtn>
      {optionsOn && (
        <Options
          loadData={loadData}
          close={() => setOptionsOn(false)}
          closeMenu={closeMenu}
          outdates={outdates}
          uploadErr={uploadErr}
        />
      )}
    </div>
  );
}

function Options({ loadData, close, outdates, closeMenu }) {
  const [msType, setMsType] = useState(0);

  useEffect(() => {
    if (outdates) close();
  }, [outdates, close]);

  const tryToLoadFromLocalStorage = () => {
    let data = localStorage.getItem("GDC_Data");
    if (data) {
      data = JSON.parse(data);
      try {
        loadData(data);
        setMsType(1);
      } catch (err) {
        setMsType(3);
      }
    } else {
      setMsType(2);
    }
  };
  const inputRef = useRef();
  const upload = () => {
    const file = inputRef.current.files[0];
    const reader = new FileReader();
    const isJson = file?.type.match(/application.*/);
    if (file?.type.match(/text.*/) || isJson) {
      reader.onload = function (event) {
        try {
          let data = JSON.parse(event.target.result);
          if (isJson) data = convertToGOOD(data);
          loadData(data);
          setMsType(4);
          if (closeMenu) closeMenu();
        } catch (err) {
          console.log(err);
          setMsType(5);
        }
      };
      reader.readAsText(file);
    }
  };
  const closeRef = useCloseModal(close);

  return (
    <ModalCarpet>
      <LoadOptions ref={closeRef} className="pt-2 pb-3 white-glow-shadow">
        <CloseBtn className="ml-auto mr-2 mb-3 glow-on-hover" onClick={close} />
        <div className="option flex-col align-center">
          <Text className="px-3 py-2" variant="h5">
            Load from Local Storage
          </Text>
          {(msType > 0 || msType < 4) && (
            <Text
              className="mb-2"
              variant="h6"
              color={msType === 1 ? "green" : "lightRed"}
              align="center"
            >
              {
                [
                  "Successfully loaded data from Local Storage",
                  "! There's no data in your Local Storage !",
                  uploadErrMs
                ][msType - 1]
              }
            </Text>
          )}
          <Button
            className="my-1"
            variant="positive"
            onClick={tryToLoadFromLocalStorage}
          >
            Load
          </Button>
        </div>
        <Divider />
        <div className="option flex-col align-center">
          <Text className="px-3 py-2" variant="h5" align="center">
            Upload a .TXT file or a .JSON file in GOOD format
          </Text>
          {msType > 3 && (
            <Text
              className="mb-2"
              color={msType === 4 ? "green" : "lightRed"}
              align="center"
            >
              {["Successfully uploaded your File", uploadErrMs][msType - 4]}
            </Text>
          )}
          <input
            ref={inputRef}
            hidden
            type="file"
            accept="text/*,application/json"
            onChange={upload}
          />
          <Button
            className="my-1"
            variant="positive"
            onClick={() => {
              inputRef.current.click();
            }}
          >
            Choose File
          </Button>
        </div>
      </LoadOptions>
    </ModalCarpet>
  );
}
