import { fullTerm } from "../../../data/acro";
import { baseSTypes } from "../../../data/misc";
import { convert } from "../../../helpers/calculators";
import { pctOf } from "../../../helpers/misc";
import { LGreen, Text } from "../../../styledCpns/DataDisplay";
import { Columns, Error, Lesser, Orange } from "./styles";
import { reactions } from "../../../data/elements";
import { atkTypes, bnFields, getTotal } from "./helpers";

export function Attributes({ ATTRs, tracker }) {
  const fields = Object.keys(ATTRs).filter((k) => k.slice(0, 4) !== "Base");
  return (
    <Columns>
      {fields.map((type) => {
        const pct = pctOf(type);
        let total = getTotal(tracker[type]);
        if (baseSTypes.includes(type)) {
          const totalPct = getTotal(tracker[`${type}%`]);
          total += Math.round((ATTRs[`Base ${type}`] * totalPct) / 100);
        }
        const err = Math.abs(total - ATTRs[type]) / ATTRs[type] >= 0.5;
        return (
          <div key={type}>
            <Text fontWeight={700}>
              {type} <Orange>{Math.round(ATTRs[type] * 10) / 10 + pct}</Orange>
            </Text>
            <div className="px-2">
              {tracker[type].map(({ desc, value }, i) => {
                return (
                  !!value && (
                    <Lesser key={i}>
                       {desc}: <LGreen>{value + pct}</LGreen>
                    </Lesser>
                  )
                );
              })}
              <ExtraPct type={type} tracker={tracker} ATTRs={ATTRs} />
              {err && <Error />}
            </div>
          </div>
        );
      })}
    </Columns>
  );
}

function ExtraPct({ type, tracker, ATTRs }) {
  if (!["HP", "ATK", "DEF"].includes(type)) return null;
  return (
    <div>
      {tracker[`${type}%`].map(({ desc, value }, i) => {
        const base = ATTRs[`Base ${type}`];
        return (
          !!value && (
            <Lesser key={i}>
               {desc}: {value}% = {base} * {value / 100} ={" "}
              <LGreen>{Math.round((base * value) / 100)}</LGreen>
            </Lesser>
          )
        );
      })}
    </div>
  );
}

export function Bonuses({ bonuses, tracker }) {
  return (
    <Columns>
      {!!bonuses.AllDmg && (
        <div>
          <Text fontWeight={700}>
            All DMG Bonus <Orange>{getTotal(tracker.AllDmg)}%</Orange>
          </Text>
          <div className="px-3">
            {tracker.AllDmg.map(({ desc, value }) => (
              <Lesser key={desc}>
                 {desc}: <LGreen>{value}%</LGreen>
              </Lesser>
            ))}
          </div>
        </div>
      )}
      {atkTypes.map((atkType) => {
        if (bnFields.every((field) => !tracker[atkType][field].length)) {
          return null;
        }
        return (
          <div key={atkType}>
            <Text color="orange">
              {fullTerm[atkType] || atkType + " Attack"}
            </Text>
            {bnFields.map((field) => (
              <Field
                key={field}
                list={tracker[atkType][field]}
                field={field}
                bonus={bonuses[atkType][convert[field]]}
              />
            ))}
          </div>
        );
      })}
      {reactions.map((rxn) => {
        if (!tracker[rxn].length) return null;
        return (
          <div key={rxn}>
            <Text fontWeight={700}>
              {rxn} <Orange>{getTotal(tracker[rxn])}%</Orange>
            </Text>
            <div className="px-3">
              {tracker[rxn].map(({ desc, value }, i) => (
                <Lesser key={i}>
                   {desc}: <LGreen>{value}%</LGreen>
                </Lesser>
              ))}
            </div>
          </div>
        );
      })}
    </Columns>
  );
}

function Field({ list, field, bonus }) {
  if (!list.length) return null;
  const pct = ["flat", "mult"].includes(field) ? "" : "%";
  return (
    <div className="mt-1 px-2">
      <Text fontWeight={700}>
        {fullTerm[field]}{" "}
        <Orange>
          {getTotal(list)}
          {pct}
        </Orange>
      </Text>
      <div className="px-2">
        {list.map(({ desc, value }) => (
          <Lesser key={desc}>
             {desc}:{" "}
            <LGreen>
              {value}
              {pct}
            </LGreen>
          </Lesser>
        ))}
        {getTotal(list) !== bonus && <Error />}
      </div>
    </div>
  );
}
