export const fullTerm = {
  NA: "Normal Attack",
  CA: "Charged Attack",
  PA: "Plunging Attack",
  ES: "Elemental Skill",
  EB: "Elemental Burst",
  cRate: "CRIT Rate",
  cDmg: "CRIT DMG",
  flat: "Flat DMG Bonus",
  pct: "Percent DMG Bonus",
  mult: "Special Multiplier"
};

export function getAcro(word) {
  for (const acro in fullTerm) {
    if (fullTerm[acro] === word) {
      return acro;
    }
  }
}
