import { wikia } from "../../data/misc";
import { useCloseModal } from "../../helpers/hooks";
import { Img, Text } from "../../styledCpns/DataDisplay";
import { AbsoluteBox, Box, ModalCarpet } from "../../styledCpns/Layout";
import { CloseBtn } from "../CommonCpns";
import { PrePickerIconWrapper } from "./styles";

export default function PrePicker({ close, choices, pick }) {
  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="p-3 relative white-glow-shadow"
        borderRadius="1rem"
        bgColor="cardBg3"
      >
        <AbsoluteBox top="0.5rem" right="0.5rem">
          <CloseBtn onClick={close} />
        </AbsoluteBox>
        <Text className="mt-2" variant="h3" align="center">
          Choose a Type
        </Text>
        <div className="mt-3 mb-1 flex">
          {Object.entries(choices).map((pair, i) => (
            <PrePickerIconWrapper
              key={i}
              className="mx-1 p-1 pointer"
              onClick={() => pick(pair[0])}
            >
              <Img
                width="100%"
                src={wikia + pair[1] + ".png"}
                alt={pair[0]}
                draggable={false}
              />
            </PrePickerIconWrapper>
          ))}
        </div>
      </Box>
    </ModalCarpet>
  );
}
