import Yelan from ".";
import { getXtraTalentLv } from "../../../../calculators/helpers";
import { Green, Hydro, LGreen } from "../../../../styledCpns/DataDisplay";
import {
  bowCAs,
  BowNaDesc,
  BowCaDesc,
  BowPaDesc,
  lightPAs,
  TalentLvMults
} from "../../config";

const actvTalents = [
  {
    type: "Normal Attack",
    name: "Stealthy Bowshot",
    image: "a/a8/Character_Yelan_Thumb",
    desc: [
      { heading: "NA", content: BowNaDesc(4) },
      {
        heading: "CA",
        content: (
          <>
            {BowCaDesc(
              "flowing water",
              "A fully charged torrential arrow",
              "Hydro"
            )}
            <br />
            Additionally, Yelan will enter a Breakthrough state after spending
            5s off-field, which will cause her next Charged Aimed Shot to have{" "}
            <Green>80%</Green> <LGreen>decreased charge time</LGreen>, and once
            charged, she can fire a Breakthrough Barb that will deal AoE{" "}
            <Hydro>Hydro DMG</Hydro> based on Yelan's <LGreen>Max HP</LGreen>.
          </>
        )
      },
      {
        heading: "PA",
        content: BowPaDesc
      }
    ],
    stats: [
      {
        name: "1-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 40.68,
        multType: 1
      },
      {
        name: "2-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 39.04,
        multType: 1
      },
      {
        name: "3-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 51.6,
        multType: 1
      },
      {
        name: "4-Hit (x2)",
        dmgTypes: ["NA", "Physical"],
        baseMult: 32.51,
        multType: 1
      },
      ...bowCAs,
      {
        name: "Breakthrough Barb DMG",
        baseSType: "HP",
        dmgTypes: ["CA", "Elemental"],
        baseMult: 11.58,
        multType: 2
      },
      {
        name: "Special Breakthrough Barb DMG (C6)",
        baseSType: "HP",
        dmgTypes: ["CA", "Elemental"],
        baseMult: 0,
        multType: 2,
        conditional: true,
        getTlBnes: ({ char, party }) => {
          if (char.constellation >= 6) {
            const lv =
              char["Normal Attack"] +
              getXtraTalentLv(Yelan.actvTalents[0], char, party);
            return {
              mult: 18.0648 * TalentLvMults[2][lv]
            };
          }
          return {};
        }
      },
      ...lightPAs("bow")
    ]
  },
  {
    type: "Elemental Skill",
    name: "Lingering Lifeline",
    image: "a/a8/Character_Yelan_Thumb",
    desc: [
      {
        content: (
          <>
            Fires off a Lifeline that tractors her in rapidly, entangling and
            marking opponents along its path.
            <br />
            When her rapid movement ends, the Lifeline will explode, dealing{" "}
            <Hydro>Hydro DMG</Hydro> to the marked opponents based on Yelan's{" "}
            <LGreen>Max HP</LGreen>.
          </>
        )
      },
      {
        heading: "Tap",
        content: <>Moves a certain distance forward rapidly.</>
      },
      {
        heading: "Hold",
        content: (
          <>
            Moves forward rapidly continuously.
            <br />
            During this time, Yelan can control her sprint direction and can end
            her rapid movement by using this Skill again.
          </>
        )
      },
      {
        content: (
          <>
            Additionally, each opponent marked by the Lifeline when it explodes
            grants Yelan a <Green>34%</Green> <LGreen>chance</LGreen> to obtain
            Breakthrough.
          </>
        )
      }
    ],
    getXtraLv: (char) => (char.constellation >= 5 ? 3 : 0),
    stats: [
      {
        name: "Skill DMG",
        baseSType: "HP",
        dmgTypes: ["ES", "Elemental"],
        baseMult: 22.61,
        multType: 2
      }
    ],
    otherStats: () => [
      {
        name: "Max Duration (Hold)",
        value: "3s"
      },
      {
        name: "CD",
        value: "10"
      }
    ]
  },
  {
    type: "Elemental Burst",
    name: "Depth-Clarion Dice",
    image: "a/a8/Character_Yelan_Thumb",
    desc: [
      {
        content: (
          <>
            Deals AoE <Hydro>Hydro DMG</Hydro> and creates an Exquisite Throw,
            which aids her in battle.
          </>
        )
      },
      {
        heading: "Exquisite Throw",
        content: (
          <>
            Swiftly fires a Hydro-imbued magic arrow, dealing AoE{" "}
            <Hydro>Hydro DMG</Hydro> and applying the Riptide status.
            <br />
            Returns a portion of its Energy Cost after use.
          </>
        )
      }
    ],
    getXtraLv: (char) => (char.constellation >= 3 ? 3 : 0),
    stats: [
      {
        name: "Skill DMG",
        baseSType: "HP",
        dmgTypes: ["EB", "Elemental"],
        baseMult: 7.31,
        multType: 2
      },
      {
        name: "Exquisite Throw DMG (x3)",
        baseSType: "HP",
        dmgTypes: ["EB", "Elemental"],
        baseMult: 4.87,
        multType: 2
      },
      {
        name: "Additional Water Arrow DMG (C2)",
        baseSType: "HP",
        dmgTypes: ["EB", "Elemental"],
        baseMult: 0,
        multType: 2,
        conditional: true,
        getTlBnes: ({ char }) => ({
          mult: char.constellation >= 2 ? 14 : 0
        })
      }
    ],
    otherStats: () => [
      {
        name: "Duration",
        value: "15s"
      },
      {
        name: "CD",
        value: "18s"
      }
    ],
    energyCost: 70
  }
];

export default actvTalents;
